    margin: 50px auto;
    animation: transitionButton 1s;
}

.learnMore-btn{ 
    padding: 20px 30px;
    font-size: 1.4rem;
    background: none;
    outline: 0;
    border: 2px solid rgb(82, 82, 82);
    cursor: pointer;
    position: relative;
    background-color: rgba(0, 0, 0, 0);
}

.learnMore-btn::after{
    content: "";
    background-color: #76a8b1;
    width: 100%;
    z-index: -1;
    position: absolute;
    height: 100%;
    top: 7px;
    left: 7px;
    transition: 0.2s;
}

.learnMore-btn:hover::after{
    top: 0px;
    left: 0px;
}

.learnMore-btn:active{
    transform: translateY(2px);
}

